.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background:#521751;
    height: 56px;
}

.toolbar_logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.toolbar_logo {
   margin-left: 1rem;
}


.toolbar_navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.spacer {
   flex: 1;
}

toolbar_navigation_items ul {
    list-style: none;
    margin: 0;
    adding: 0;
    display: flex;
}

toolbar_navigation_items li {
    padding: 0 0.5rem;
}

.toolbar_navigation_items a{
color: white;
    text-decoration: none;
}


.toolbar_navigation_items a:hover,
.toolbar_navigation_items a:active {
    color: #fa923f;
}