.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 22px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.toggle-button:focus {
 outline: none;
}

.toggle-button_line {
    width: 30px;
    height: 2px;
    background: white;
}